import DOFBaseLayer from '../bases/DOFBaseLayer';

let values = [
    {
        display: 'linear',
        label: 'Linear Tween',
        value: 'linear'
    },
    {
        display: 'sine in',
        label: 'Sine Ease In',
        value: 'sineIn'
    },
    {
        display: 'sine out',
        label: 'Sine Ease Out',
        value: 'sineOut'
    },
    {
        display: 'sine in out',
        label: 'Sine Ease In Out',
        value: 'sineInOut'
    },
    {
        display: 'cubic in',
        label: 'Cubic Ease In',
        value: 'cubicIn'
    },
    {
        display: 'cubic out',
        label: 'Cubic Ease Out',
        value: 'cubicOut'
    },
    {
        display: 'cubic in out',
        label: 'Cubic Ease In Out',
        value: 'cubicInOut'
    }
];

let Tween = {
    type: {
        name: 'enum',
            values
    },
    defaultValue: values[3],
        blendOperation: 'none'
};

let RotationType = {
    type: "float",
    defaultValue: 0.0,
    blendOperation: "add"
};

let BodyInfo = {
    layerType: "Body",
    properties: {
        Head: RotationType,
        Torso: RotationType,
        Pelvis: RotationType,
        Tween
    },
    dofs: {
        topSection_r: true,
        middleSection_r: true,
        bottomSection_r: true
    }
};

export default class BodyLayer extends DOFBaseLayer {

    static getInfo(){
        return BodyInfo;
    }

    constructor() {
        super();
    }

    static generateDofs(props){
        return {
            topSection_r: props.Head/180 * Math.PI,
            middleSection_r: props.Torso/180 * Math.PI,
            bottomSection_r: props.Pelvis/180 * Math.PI
        };
    }
}